const formatDistanceLocale = {
  lessThanXSeconds: {
    one: " ",
    two: "  ",
    other: " {{count}} ",
  },

  xSeconds: {
    one: "",
    two: " ",
    other: "{{count}} ",
  },

  halfAMinute: " ",

  lessThanXMinutes: {
    one: " ",
    two: "  ",
    other: " {{count}} ",
  },

  xMinutes: {
    one: "",
    two: " ",
    other: "{{count}} ",
  },

  aboutXHours: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  xHours: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  xDays: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  aboutXWeeks: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  xWeeks: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  aboutXMonths: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  xMonths: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  aboutXYears: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  xYears: {
    one: "",
    two: "",
    other: "{{count}} ",
  },

  overXYears: {
    one: " ",
    two: " ",
    other: " {{count}} ",
  },

  almostXYears: {
    one: " ",
    two: " ",
    other: " {{count}} ",
  },
};

export const formatDistance = (token, count, options) => {
  // Return word instead of `in one day` or `one day ago`
  if (token === "xDays" && options?.addSuffix && count <= 2) {
    if (options.comparison && options.comparison > 0) {
      return count === 1 ? "" : "";
    }

    return count === 1 ? "" : "";
  }

  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else if (count === 2) {
    result = tokenValue.two;
  } else {
    result = tokenValue.other.replace("{{count}}", String(count));
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return " " + result;
    } else {
      return " " + result;
    }
  }

  return result;
};
